import React from "react"
import { Link } from "gatsby"
import { Search, Clear } from "./icons"
import Logo from "../../content/assets/logo.png"

import "./navbar.scss"

const NavBar = ({title, toggleDarkMode}) => {
  const placeHolderText = `Generate Tweets for @user`

  return (
    <>
      <div className={`navbar`}>
        <div className={`navbar-inner`}>

          <Link to={`/`}>
            <img className={`navbar-logo`} alt={`logo`} src={Logo} />
          </Link>
          
          <div className={`search-bar`}>
            <span className={`search-icon-wrapper`}>{Search}</span>
            <span className={`search-box`}><input placeholder={placeHolderText} /></span>
            <span className={`clear-icon-wrapper`}>{Clear}</span>
          </div>

          <button className={`dark-mode-toggle`} onClick={toggleDarkMode}><span role={`img`} aria-label={`moon-emoji`}>🌛</span></button>

        </div>
      </div>
    </>
  )
}

export default NavBar
